.main-container {
    background-image: linear-gradient(
        66.99deg, 
        rgba(0, 0, 0, 0.4) 0%, 
        rgba(68, 68, 68, 0.29) 48.36%, 
        rgba(255, 255, 255, 0) 121.27%), 
        url(/static/media/hills.f345375c.jpg);
        visibility: visible;
        height: 770px;
        width: 100%;
        overflow: hidden;
        background-size: 100% 100%;
}

.sub-header-top {
    /* RestEasy */
    position: absolute;
    width: 257px;
    height: 59px;
    top: 224px;

    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 59px;
    /* identical to box height */

    color: #FFFFFF;
}

.main-header {
    /* Registry */
    position: absolute;
    width: 443px;
    height: 117px;
    top: 268px;

    font-style: normal;
    font-weight: bold;
    font-size: 96px;
    line-height: 117px;
    /* identical to box height */

    color: #FFFFFF;
}

.header-description {
    /* With a customized RestEasy registry you can redirect sympathetic gifts toward something more productive or meaningful. */
    position: absolute;
    width: 634px;
    height: 95px;
    top: 400px;

    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;

    color: #FFFFFF;
}

.get-started {
    /* Get Started Now: */

    position: absolute;
    width: 321px;
    height: 46px;
    top: 510px;

    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    line-height: 44px;

    color: #FFFFFF;

}

.loved-name {
    /* Get Started Input */
    position: absolute;
    width: 240px;
    height: 31px;
    left: 325px;
    top: 498px;
    padding-bottom: 36px;
    border-bottom-width: 2px;
    border-bottom-color: #FFFFFF;
    border-bottom-style: solid;
}

.loved-name-input {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: transparent;
    border: none;
}
.loved-name-input:focus {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: transparent;
    border: none;
    outline: none;
    color: white;
}

.btn-get-started {
    background-color: transparent;
    border: 0;
}
.btn-get-started:hover {
    background-color: transparent;
    border: 0;
}

.tagline{
    /* Commemoration is expensive. We are often conflicted between wanting the 
    best for our loved ones and the high cost of the best. The RestEasy Registry
    feature is designed to eliminate that conflict. */


    width: 1000px;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 34px;
    align-items: center;
    text-align: center;

    color: #7B7B7B;
    margin-top: 30px;
    margin-bottom: 30px;
}
.commemoration {
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 34px;
    align-items: center;
    text-align: center;

    color: #7B7B7B;
}

.eliminate{
    font-style: italic;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    align-items: center;
    text-align: center;

    color: #7B7B7B;
}

.divider-howItWorks{
    background: #8FC36B;
    height: 50px;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 28px;
    height: 85px;
}

.option-row{
    margin-bottom: 50px
}
.option {
    height: 50px;
    width: 170px;
    color: #7B7B7B;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    margin-left: 10px;
    border-radius: 5px;
    cursor: pointer;
}
.option:not(.active):hover {
    background-color: rgb(214, 214, 214);
    
}
.active{
    background-color: #8FC36B;
    color: white
}

.outer-box {
    width: 1162px;
    height: 600px;
    background-color: #8FC36B;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}
.box {
    width: 1062px;
    height: 500px;
    box-shadow: 0px 0px 40px rgb(255, 255, 255);
    border-radius: 10px;
    background-color: white;
    padding-top: 5px;
    padding-left: 40px;
    padding-right: 40px;
}
.div-line{
    display: block; 
    border: 1px solid rgba(0, 0, 0, 0.2);
    width: 500px;
    margin-top: 50px
    
}
.other-q{
    margin: 50px;
    font-size: 30px;
    text-align: center;
}
.header{
    margin-bottom: 75px;
}
.restEasy-logo{
    width: 85px;
    height: auto;
}
.title{
    font-size: 30px;
    font-weight: bold;
    padding-right: 10px;
    cursor: pointer;
}

.menu-options {
    padding-top: 10px;
    font-size: 16px;
    color: #8FC36B;
}
.search-area{
    display: block;
}
.search-bar {
    /* margin-right: 30px;
    background-color: rgba(255, 255, 255, 0.548);
    border-width: 5;
    border-radius: 50; */
    background: rgba(255, 255, 255, 0.952);
    border: #8FC36B;
    border-style: solid;
    border-radius: 50px;
}
.search-bar:focus {
    background: rgba(255, 255, 255, 0.336);
    border: none;
    color:#8FC36B;

}
.search-bar::-webkit-input-placeholder {
    color: #8FC36B
}
.search-bar:-ms-input-placeholder {
    color: #8FC36B
}
.search-bar::-ms-input-placeholder {
    color: #8FC36B
}
.search-bar::placeholder {
    color: #8FC36B
}
.navBar-custom {
    background-color: rgba(255, 255, 255, 0.952);
    box-shadow: 0px 3px 5px 2px #00000013;
    height: 75px;
}
.header-link {
    color: #8FC36B;
}
.username {
    margin-right: 30px;
}
.navbar-collapse.show, .navbar-collapse.collapsing{
    background: rgba(255, 255, 255, 0.959);
    text-align: center;
    border-radius: 5%;
    box-shadow: 0px 3px 5px 2px #00000013;

}

.form-inline{
    display: block;
    margin-left: 20px;
    margin-bottom: 15px;
    margin-top: 15px
}
.sign-in-out{
    font-size: 16px;
}
.search-bar {
    font-size: 16px
}

@media only screen and (max-width: 1360px) {
    .header-link {
        font-size: 13px;
    }
    .username {
        margin-right: 30px;
        font-size: 13px;
    }
    .sign-in-out{
        font-size: 13px;
    }
    .search-bar {
        font-size: 13px
    }
}
@media only screen and (max-width: 1200px) {
    .header-link {
        font-size: 10px;
        font-weight: bold;
    }
    .username {
        margin-right: 10px;
        font-size: 10px;
        font-weight: bold;
    }
    .sign-in-out{
        font-size: 10px;
        font-weight: bold;
    }
    .search-bar {
        font-size: 10px;
        font-weight: bold;
    }
}
@media only screen and (max-width: 990px) {
    .header-link {
        font-size: 14px;
    }
    .username {
        margin-right: 30px;
        font-size: 14px;
    }
    .sign-in-out{
        font-size: 14px;
    }
    .search-bar {
        font-size: 14px
    }
    .restEasy-logo{
        width: 75px;
        height: auto;
    }
}
