.overlay {
    position: absolute;
    top: -1;
    bottom: 1;
    left: -1;
    right: 1;
    height: 101%;
    width: 101%;
    border-radius: 15px;
    opacity: 0;
    transition: .5s ease;
    background-color: #90c36b;
}
  
.card:hover .overlay {
    opacity: 1;
}
